/*==========  Non-Mobile First Method  ==========*/
.module-with-icon-mobile {
  display: none;
}

@media only screen and (max-width : 1500px) {
  #slider-4-people {
    .overlay {
      .overlay-content {
        .title {
          font-size: 23px;
        }
      }
    }
  }
}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  #slider-4-people {
    .overlay {
      .overlay-content {
        .title {
          font-size: 18px;
        }
      }
    }
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
  .career .carousel {
      display: block;
  }
  .company-page .carousel {
      display: block;
  }
  .module-with-icon-mobile {
    display: block;
    @include phone {
      padding: 20px 0 30px 0;
      border-bottom: 1px solid #d9d9d9;
    }
  }
  .career {
    .has-border-bottom {
      .col-sm-4:first-child {
        padding-left: 15px;
      }
    }

    .slide-text {
      .item {
        padding: 0 15px;
      }
    }
      .carousel-indicators {
        /* bottom: 10px; */
        left: 15px;
      }
  }

  .partner {
    padding: 50px 0;
  }
}

