.gender {
	.star {
		font-size: 18px;
		color: #c3c3c3;
		position: absolute;
		right: 105px;
		top: 0;
	}
}

#slider-5, #slider-9 {
	.carousel-inner {
		.item {
			img {
				width: 100%;
			}
		}
	}
}
.meet-our-team__slider, .working-at-ktm__slider, .people-in-ktm__slider {
    .map-section {
        height: auto;
    }
}
.people-in-ktm__slider {
    .section-grid .grid-section .item-grid.item-text .overlay {
        background: #e4e4e4;
    }
    .overlay {
        .overlay-content {
            .title, p {
                color: #000;
            }
        } 
    }
}

.slide-inner-ktm {
	.item {
		padding: 0 50px;
		overflow: hidden;
	}
}

@include phone {
	.phone-hidden {
		display: none;
	}
}