html, body  {
    font-size: 16px;
}

a {
    &:active, &:focus, &:hover {
        outline: none !important;
    }
}
.no-gutter {
    padding: 0;
}
$section-padding:64px;
.container {
    @include respond-to-min($largeSize) {
        width: rem(1630px);
    }
}

.triple-container {
    @include respond-to-min($tabletSize) {
        padding-left: rem($tripple-space-tablet);
        padding-right: rem($tripple-space-tablet);
    }
    @include respond-to-min($largeSize) {
        padding-left: rem($tripple-space);
        padding-right: rem($tripple-space);
    }
}
.tripple-row {
    @include respond-to-min($tabletSize) {
        margin-left: rem(-$tripple-space-tablet);
        margin-right: rem(-$tripple-space-tablet);
    }
    @include respond-to-min($largeSize) {
        margin-left: rem(-$tripple-space);
        margin-right: rem(-$tripple-space);
    }
}
.tripple-column-space {
    @include respond-to-min($tabletSize) {
        padding-left: rem($tripple-space-tablet);
        padding-right: rem($tripple-space-tablet);
    }
    @include respond-to-min($largeSize) {
        padding-left: rem($tripple-space);
        padding-right: rem($tripple-space);
    }
    &.no-padding-left {
        @include respond-to-min($largeSize) {
            padding-left: 0;
        }
    }
    &.no-padding-right {
        @include respond-to-min($largeSize) {
            padding-right: 0;
        }
    }
}
.table-style {
    @include respond-to-min($tabletSize) {
        display: table;
    }
}
.table-row {
    @include respond-to-min($tabletSize) {
        display: table-cell;
        float: none;
        vertical-align: top;
    }
}
.dark-background {
    position: relative;
    background: rgba(0, 0, 0, 0.6);
    height: 100%;
    .has_bg_img {
        background-repeat:no-repeat;
        background-position: right 0;
        display: block;
        right: 0;
        background-size: cover;
        position: absolute;
        height: 100%;
        width: rem(1900px);
        @include respond-to-max($tabletSize) {
            background-size: cover;
        }
    }

    &.bg-img-share-price {
        &:before {
            background: url(../images/share-price-bg.jpg) no-repeat right 0;
            right: 0;
            background-size: 60%;
            @include respond-to-max($tabletSize) {
                background-size: 40%;
            }
        }
    }

    &.bg-img-general-meeting {
        &:before {
            background: url(../images/general-meeting-bg.jpg) no-repeat right 0;
            right: 0;
            background-size: 60%;
            @include respond-to-max($tabletSize) {
                background-size: 40%;
            }
        }
    }

    &.location-bg {
        &:before {
            background: url(../images/slide-2-480.jpg) no-repeat right 0;
            right: -10%;
            background-size: 60%;
            @include respond-to-max($tabletSize) {
                background-size: 40%;
            }
        }
    }

    &:before {
        background: rgba(0, 0, 0, 0.6);
        width: 1920px;
        height: 100%;
        position: absolute;
        right: 100%;
        top: 0;
        content: ' ';
    }
}
.seperate-line {
    position: relative;
    @include respond-to-min($tabletSize) {
        background: url('../images/dot.jpg') repeat-y right rem(35.5px) top;
    }
    @include respond-to-min($largeSize) {
        background-position: right rem(70.5px) top;
    }
    /*&:after {
        position: absolute;
        top: 0;
        right: rem($tripple-space-tablet);
        width: rem(1px);
        height: 100%;
        background: #d9d9d9;
        content: ' ';
        display: none;
        @include respond-to-min($tabletSize) {
            display: block;
        }
        @include respond-to-min($largeSize) {
            right: rem($tripple-space);
        }
    }*/

    @include respond-to-max($tabletSize) {
        .intro-readmore {
            display: none;
        }

        .outer-main-intro {
            margin-bottom: 30px;
            top: 0 !important;
        }
    }
}
.owl-theme .owl-dots {
    opacity: 0;
    pointer-events: none;
}
div > .block {
    &:first-child {
        section {
            padding-top: rem(35px);
            @include respond-to-min($tabletSize) {
                padding-top: rem(100px);
            }
            @include respond-to-min($desktopSize) {
                padding-top: rem(150px);
            }
            @include respond-to-min($largeSize) {
                padding-top: rem(195px);
            }
            .padding-section {
                padding-top: 0;
                padding-bottom: 0;
            }
        }
    }
}