@import "vars";

.contact-us {
	background-image: none;

	.row-intro {
		border: none;
	}

	.guide-to-webs {
		margin-right: 20px;
	}

	.post-detail {
		.address {
			margin-top: 35px;
		}
	}
    .information-block {
        margin-bottom: rem(95px);
		@include respond-to-max($tabletSize) {
			margin-bottom: rem(30px);
		}
    }
}

.post-detail {
	&:before {
		content: ' ';
		display: block;
		background: #d9d9d9;
		height: 1px;
		width: 90%;
		position: absolute;
		bottom: 0;
		left: 35px;
	}
	@include respond-to-max($tabletSize) {
		&:before {
			left: 15px;
		}
	}
}

.selected-new {
	.col-sm-7 {
		.post-detail {
			@include respond-to-min($tabletSize) {
				&:before {
					height: 0;
				}
			}
		}
	}
}

.post-title {
	h3 {
		margin-top: 0;
	}
}

.input-field {
	margin-top: 20px;

	.alert-message, .at-error, .at-warning {
		position: relative;
		width: 100%;
		background: #ff6c6c;
		color: white;
		padding: 10px;
		margin: 0;
		
		&:before {
			position: absolute;
			border: 15px solid transparent;
			border-top-color: #ff6c6c;
			right: 6px;
			bottom: -23px;
			content: "";
		}
		.arrowdown {
			position: absolute;
			border: 15px solid transparent;
			border-top-color: #ff6c6c;
			right: 6px;
			bottom: -23px;
		}
	}
}
.link-btns {
	background: transparent;
    border: 0;
    text-transform: uppercase;
    margin: 10px 0 0;
    padding: 0;
}
.mobile-number-wrapper {
	.alert-message {
		display: block;
	}
}
.input-field {
	position: relative;
	.glyphicon {
		top: 47px;
		right: 4.5px;
		&.glyphicon-ok {
			display: none;
		}
		&:before {
			color: #FF6C6C;
		}
	}
}


.short-dropdown {
	select.subject {
		appearance: none;
		-webkit-appearance: none;
		-moz-appearance: none;
		background-position: 90% 50%;
	}
}
.subject-dropdown {
	background: white url(../images/dropdown-icon.png) no-repeat center right;
	background-size: contain;

	select.subject {
		appearance: none;
		-webkit-appearance: none;
		-moz-appearance: none;
		background-position: 90% 50%;
	}
}

.short-dropdown {
	width: 70px;
	display: inline-block;
	*display: inline;
	*zoom: 1;
	margin-left: 10px;
	select {
		padding: 10px 0 10px 0;
		background: url(../images/dropdown-icon.png) no-repeat top right;
		background-size: contain;
	}
}

textarea.message {
	height: 293px;
	resize: none;
	background-size: 12%;
}

.robot-text {

	a {
		color: black;
	}
	#star, .fa-umbrella, .fa-star {
		color: #9c9a98;
	}

	.fa {
		margin: 0 5px;
	}
}

input, select, textarea {
	border: 1px solid #d9d9d9;
	@include respond-to-max($tabletSize) {
		margin: 0;
		padding: 0;
	}
}

.alert-message {
	display: none;
}

input[type="text"], select, textarea {
	width: 100%;
	padding: 10px 40px 10px 10px;
        margin: 0;
}

input[type="checkbox"] {
		margin: 0 5px 0 0;
        display: inline-block;
        vertical-align: middle;
}

.male {
		margin-right: 20px;
        @include respond-to-min($desktopSize) {
			padding-right: 30px;
        }
}

.gender {
	clear: both;
	padding-left: 15px;
    span {
        display: inline-block;
        vertical-align: middle;
    }
	.male, .female {
		float: left;
	}
    .star {
        position: static;
        margin-right: rem(5px);
    }
    .text-right {
        @include respond-to-max($tabletSize) {
            text-align: left;
        }
    }
}

.valid {
	background: white url(../images/valid.png) no-repeat center right;
	background-size: contain;
}

.error {
	background: white url(../images/warning.png) no-repeat center right;
	background-size: contain;
	border: 1px solid #ff6c6c;
}

.required {
	background: transparent url(../images/required.png) no-repeat right top;
	background-size: contain;
	&.at-required, &.at-invalid {
		background: transparent;
	}
}
