%ul-reset {
    list-style: none;
    font-size: 0;
    padding-left: 0;
    margin-bottom: 0;
}

%icon {
    background-image: url('../images/icon.png');
    background-repeat: no-repeat;
    background-position: 0 0;
}

input {
    outline: none;
}

.po-s {
    position: static !important;
}
.ov-v {
    overflow: visible !important;
}

.fw7 {
    font-weight: 700;
}

.pl0 {
    padding-left: 0 !important;
}

.none {
    display: none !important;
}

.mt0 {
    margin-top: 0 !important;
}

.gray-bg {
    background: #5c5c5c;
}

p {
    line-height: 1.7;
}

h3 {
    font-size: 28px;

    @include respond-to-max($tabletSize) {
        font-size: 22px;
    }
}

.outer-main-intro {
    padding-left: 0;
    &.has-dropdown {
        @include respond-to-min($tabletSize) {
            padding-right: 0;
        }
    }
}

.padding-right {
    .outer-main-intro {
        padding-right: 0;
    }
}


body {
    color: #000;
    font-family: $font-base;
    font-size: 15px;
}

section, .carousel {
    overflow: hidden;
}

h2,
h3,
h4 {
    font-family: $font-second;
}

.hr-indicators {
    margin-top: 0px !important;
    margin-bottom: 0px !important;
    border: 0 !important;
    border-top: none;
    height: 0px;
}

header {
    position: relative;
    padding: 50px 0 0;
    background: white;
    z-index: 3;
    .logo {
        margin: 0 0 35px;
    }
    .main-nav {
        @extend %ul-reset;
        > li {
            display: inline-block;
            margin-right: 30px;
            a {
                font-family: $font-base;
                font-size: 18px;
                color: #000;
                text-transform: uppercase;
                &:hover {
                    text-decoration: none;
                }
            }
            > a {
                display: block;
                padding-bottom: 40px;
                position: relative;
                &:hover {
                    &:before {}
                }
            }
        }
        .function-addon {
            margin: 0;
            width: 75px;
            text-align: center;
            .sub-menu {
                text-align: left;
            }
            a {
                >span {
                    &:after {
                        content: "";
                        width: 1px;
                        height: 25px;
                        background: #000;
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }
        }
    }
    #magic-line {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 8px;
        background: #000;
    }
}

.sub-menu {
    position: absolute;
    width: 100%;
    top: 100px;
    left: 0;
    z-index: 1;
    background: rgba(0, 0, 0, .5);
    visibility: hidden;
    opacity: 0;
    transition: all 0.5s ease;
    ul {
        list-style: none;
        margin: 10px 0;
        li {
            display: inline-block;
            margin: 10px 25px 10px 0;
            padding-right: 25px;
            position: relative;
            opacity: 0;
            top: -30px;
            &:before {
                content: "|";
                position: absolute;
                right: 0;
                top: -1px;
                color: #fff;
                display: block;
                font-size: 18px;
            }
            &:last-child {
                margin-right: 0;
                padding-right: 0;
                &:before {
                    display: none;
                }
            }
            a {
                color: #fff;
                font-size: 18px;
                text-transform: uppercase;
                display: inline-block;
                text-decoration: none;
                &:after {
                    content: '';
                    display: block;
                    height: 3px;
                    width: 0;
                    background: transparent;
                    transition: width .5s ease, background-color .5s ease;
                }
                &:hover:after {
                    width: 100%;
                    background: white;
                }
            }
        }
    }
}

.ktm-icon {
    display: inline-block;
    *display: inline;
    *zoom: 1;
}

.ktm-icon-search {
    width: 46px;
    height: 46px;
    @extend %icon;
}

.ktm-icon-search-mini {
    width: 16px;
    height: 16px;
    @extend %icon;
    background-position: -50px 0;
}

.ktm-icon-globe {
    width: 16px;
    height: 16px;
    @extend %icon;
    background-position: -50px -18px;
}

.ktm-icon-double-right {
    position: relative;
    top: -5px;
    font-size: 125%;
    line-height: 0.4;
    vertical-align: middle;
    left: -15px;

    @include respond-to-min($desktopSize) {
        left: -39px;
        top: -11px;
    }

    @include respond-to-min($largeSize) {
        top: -18px;
        left: -52px;
    }
}

.search-form {
    position: relative;
    .input-form {
        width: 100%;
        font-size: 50px;
        margin: 0;
        padding: 10px 0 10px 65px;
        font-family: $font-base;
        color: #fff;
        background: transparent url('../images/search.png') no-repeat 50px center;
        border: 0;
        &:focus {
            outline: none;
        }
        &::-webkit-input-placeholder {
            color: #787878;
        }
        &::-moz-placeholder {
            color: #787878;
        }
        &:-ms-input-placeholder {
            color: #787878;
        }
    }
    span {
        position: absolute;
        top: 50%;
        left: 0;
        font-size: 50px;
        color: #6c6d71;
        transform: translateY(-50%);
    }
}

.brand-list {
    ul {
        @extend %ul-reset;
        li {
            display: inline;
            font-size: 14px;
            margin-left: 4px;
        }
    }
}

.carousel {
    position: relative;
    margin-left: 0 !important;
}
.intro-title {
    font-family: "TradeGothicBoldCon";
    font-size: rem(40px);
    margin-top: 0;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 10px;
    width: 85%;
    @include respond-to-min($tabletSize) {
        font-size: rem(40px);
    }
    @include respond-to-min($desktopSize) {
    }
    @include respond-to-min($largeSize) {
        font-size: rem(60px);
    }
}
.intro-des {
    font-size: 15px;
    margin-bottom: 30px;
    margin-right: 20px;
}
.light-color {
    .intro-title,
    .intro-des,
    .intro-readmore,
