        color: #fff;
    }
}

.dark-color {
    .intro-title,
    .intro-des,
    .intro-readmore,
    .intro-cate {
        color: #000 !important;
    }
}
.outer-main-intro {
    position: relative;
    z-index: 1;
    &:before {
        width: 20px;
        height: 6px;
        display: block;
        content: "";
        background: #000;
        position: absolute;
        top: 0;
        right: -10px;
        z-index: 2;
        .carousel & {
            display: none;
        }
        @include phone {
            display: none;
        }
    }
}
.type-of-categories {
    ul {
        @extend %ul-reset;
        li {
            border-bottom: 1px solid #d9d9d9;
            position: relative;
            &:first-child {
                border-top: 1px solid #d9d9d9;
            }
            a {
                font-size: 18px;
                font-family: $font-second;
                text-transform: uppercase;
                display: block;
                padding: 30px;
                color: #000;
                &:hover {
                    text-decoration: none;
                }
            }
            &.active,
            &:hover {
                background: #e4e4e4;
                &:before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 100%;
                    position: absolute;
                    right: 0;
                    top: 0;
                    background: #000;
                }
            }
        }
    }
}

.intro-readmore,
.intro-cate,
.submit-btn {
    text-transform: uppercase;
    font-family: "TradeGothicBoldCon", Arial, Helvetica, sans-serif;
    font-size: 18px;
    color: #000;
}

.submit-btn {
    display: block;
    /* width: 60px; */
    cursor: pointer;
}

.intro-readmore, .submit-btn {
    &:hover {
        text-decoration: none;
        color: #7d7d7d !important;

        span.fa-arrow-right {
            -webkit-animation: loop-arrow 0.8s;
            animation: loop-arrow 0.8s;
            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
        }
        span.rigth-arrow {
            -webkit-animation: loop-arrow 0.8s;
            animation: loop-arrow 0.8s;
            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
        }
        .fa-arrow-right {
            background-position: 100% 50%;
        }
    }
    span {
        position: relative;
        left: 5px;
    }
}

.intro-readmore, .submit-btn {
    margin: 15px 0;
}
