.load-more {
    text-align: center;
    border-top: 1px solid #d9d9d9;
    padding-top: 50px;
    //margin-bottom: 50px;
    font-family: "TradeGothicBoldCon", Arial, Helvetica, sans-serif;

    @include respond-to-max($tabletSize) {
        padding-top: 20px;
        //margin-bottom: 20px;
    }
    a {
        text-transform: uppercase;
        font-size: 18px;
        color: #000;
        span {
            margin-left: 20px;
        }
        &:hover {
            text-decoration: none;
        }
    }
}

.section-info {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.block-text {
    line-height: 1;
    overflow: hidden;
    border-bottom: 1px solid #d9d9d9;
    padding-top: rem(0px);
    @include respond-to-min($desktopSize) {
        padding-top: rem(0px);
    }
    &:first-child {
        margin-top: 0;
    }
    .text-focus {
        font-family: "TradeGothicBoldCon";
        font-size: 40px;
        float: left;
        border-right: 1px solid #d9d9d9;
        width: 45%;
        height: 90px;

        @include respond-to-min($largeSize) {
            font-size: 60px;
        }
        span {
            display: block;
            font-size: 18px;
            padding-top: 10px;

            @include respond-to-max($tabletSize) {
                padding-top: 0;
            }
            @include respond-to-min($desktopSize) {
                font-size: 20px;
            }
        }
        .block-focus {
            i {
                font-style: normal;
            }
        }
    }
    &.block-img {
        .text-focus {
            text-align: left;
            img {
                width: 77px;
            }
        }
    }
    .text-des {
        float: left;
        text-transform: uppercase;
        line-height: 1.6;
        width: 55%;
        font-size: 13px;
        padding-left: rem(15px);
        @include respond-to-min($tabletSize) {
        }
        @include respond-to-min($desktopSize) {
            padding-left: rem(30px);
        }
        @include respond-to-min($largeSize) {
        }
        a {
            color: #000;
            &:hover {
                text-decoration: none;
            }
        }
    }

    p {
        line-height: 1;
    }
}

.block-text:nth-child(even) {
    padding-left: 50px;
}

.row-intro {
    padding-right: 0;
}

.module-feature {
    padding: 0px 15px;
    @include respond-to-max($tabletSize) {
        .col-md-6 {
            padding: 0;
        }
    }
    @include respond-to-max($desktopSize) {
        .col-md-6 {
            margin-top: 50px;
        }

    }
    @include respond-to-min($desktopSize) {
        padding: 60px 0 0;
    }
    @include respond-to-min($largeSize) {
    }
    .block-focus {
        text-transform: uppercase;
        margin: 0;

        .animation-counter {
            float: left;
            overflow: hidden;
            height: 50px;

            @include respond-to-max($largeSize) {
                height: 35px;
            }

            .block-animated {
                position: relative;
                top: 0;
            }
        }

        i:not(.unit) {
            display: block;
        }

        span {
            clear: both;
        }
    }

    .sign {
        float: left;
    }

    .ebit,
    .revenue {
        span {
            margin-left: 0;
            @include respond-to-min($desktopSize) {
                margin-left: 21px;
            }
        }
    }
    .sign {
        font-size: 40px;
        height: 50px;

        @include respond-to-min($largeSize) {
            font-size: 60px;
        }
    }

    .unit {
        font-size: 40px;
    }

    @include respond-to-max($largeSize) {
        .unit, .sign {
            font-size: 30px;
        }
    }
    .report-downloader {
        border: none;
        .text-focus {
            background: url('../images/document.jpg') no-repeat center bottom;
            background-size: contain;
        }
        .text-des {
            .intro-readmore {
                font-family: "TradeGothicBoldCon", Arial, Helvetica, sans-serif;
                font-size: rem(18px);
                line-height: 1;
                padding-top: 6px;
                margin: 0;
                display: block;
            }
        }
    }
}

.first-child {
