            padding-top: rem(100px);
        }
        @include respond-to-min($desktopSize) {
            padding-top: rem(150px);
        }
        @include respond-to-min($largeSize) {
            padding-top: rem(195px);
        }
    }
}
.slide {
    .section-info {
        .container {
            @include respond-to-min($tabletSize) {
                height: 100%;
            }
            .row {
                @include respond-to-min($tabletSize) {
                    height: 100%;
                }
                .tripple-column-space {
                    @include respond-to-min($tabletSize) {
                        height: 100%;
                    }
                }
                > .col-xs-12 {
                    padding-left: 0;
                    padding-right: 0;
                    @include respond-to-max($desktopSize) {
                        padding-left: rem(15px);
                    }
                    &.tripple-column-space {
                        @include respond-to-min($tabletSize) {
                            padding-left: rem($tripple-space-tablet);
                            padding-right: rem($tripple-space-tablet);
                        }
                        @include respond-to-min($largeSize) {
                            padding-left: rem($tripple-space);
                            padding-right: rem($tripple-space);
                        }
                    }
                }
            }
        }
    }
    .outer-main-intro {
        padding: rem(70px 15px 20px 0px);
        @include respond-to-min($desktopSize) {
            padding: 80px 20px 80px 0;
        }
        @include respond-to-min($largeSize) {
            padding: 140px 75px 140px 0;
        }
    }
}
.padding-section {
    padding-top: rem(30px);
    padding-bottom: rem(30px);
    @include respond-to-min($tabletSize) {
        padding-top: rem(50px);
        padding-bottom: rem(50px);
    }
    @include respond-to-min($desktopSize) {
        padding-top: rem(80px);
        padding-bottom: rem(80px);
